.about {
  padding-top: 125px;
  padding-bottom: 39px;
  background: $bg-color;
}
.about__container {
  // display: flex;
}

.about__label {
  margin-bottom: 15px;
  font-family: Titan One;
  font-size: 18px;
  line-height: 1.2;
  text-align: center;
  text-transform: uppercase;
  //   цвета нет
  color: #ffa5ba;
}
.about__title {
  margin-bottom: 26px;
  font-family: Titan One;
  font-size: 34px;
  line-height: 1.1;
  text-align: center;
  letter-spacing: 0.04em;
  text-transform: uppercase;
  color: $accent-color;
}

.about__container-1 {
  // padding 30 ибо 50 - 20 от контейнера
  padding: 0 30px;
}
// через :first-child и :last-child не работает
.about__text {
  font-weight: 500;
  font-size: 12px;
  line-height: 1.6;
}
.about__text-1 {
  margin-top: 46px;
}
.about__text-2 {
  margin-top: 30px;
  color: $secondary-text-color;
}

.about__text-3 {
  display: block;
  margin-top: 10px;
}
.about__btn {
  margin-top: 17px;
  width: 176px;
  height: 44px;
  font-weight: bold;
  font-size: 14px;
  line-height: 1.3;
  border-radius: 22px;
}
.about__icon-btn {
  margin-left: 10px;
}

.about__bg-milk {
  background-image: url(../images/about/mob/bg-about.png);
  background-repeat: no-repeat;
  background-size: contain;
  background-position-y: 745px;

  @media screen and (min-width: 480px) and (max-width: 767px) {
    background-image: url(../images/about/mob/bg-about.png);
    background-repeat: no-repeat;
    background-size: contain;
    background-position-y: 577px;
  }
}
